--------------------------------------------------------
  layouts/_l-flag.scss
--------------------------------------------------------

  The flag object shares a lot of common traits with the media object; its sole purpose is displaying image- and text-like content side-by-side. Where the flag object differs, however, is its ability to vertically align the image and text to the tops, middles or bottoms of each other. Aside from the visual difference of vertical alignment, there isnt too much difference between the two objects code. One thing to note is that the flag object is slightly more verbose in its implementation; the media object can have its classes applied directly to its content whereas the flag object needs its classes applied to wrappers around its content. For example, compare the following snippets for the media and flag objects respectively:

  Example: Flag Object
  <div class="l-flag">
      <div class="l-flag__image">
