  .tooltip[data-placement="left"]:before,
  .tooltip[data-placement="left"]:after {
    right: 100%; }
  .tooltip[data-placement="left"]:after {
    top: 50%;
    margin-right: 0.9375rem;
    transform: translateY(-50%);
    border-radius: 0.25rem; }
  .tooltip[data-placement="left"]:before {
    top: 50%;
    margin-right: 0.46875rem;
