    border-bottom: 0.5rem solid transparent;
    border-left: 0.5rem solid #000;
    content: ''; }
  .tooltip.is-active:hover:before,
  .tooltip.is-active:hover:after,
  .tooltip.is-active:focus:before,
  .tooltip.is-active:focus:after {
    transition: opacity 0.3s linear 0s;
    opacity: 1;
    visibility: visible;
    z-index: 2; }

/**

  @post
--------------------------------------------------------
  modules/_post.scss
--------------------------------------------------------

  Blog Post styling - .post being the class that wraps around each article item

-----------------------------------------------------  */
.post {
  margin-bottom: 1.5rem;
  padding: 1.5rem 0 0.5625rem;
  border-bottom: 1px solid #cfd2d2; }

.post__title {
  margin-bottom: 0.75rem; }

.post__time {
  display: block;
  margin-bottom: 1.5rem;
  font-size: smaller; }

/**

  @section
--------------------------------------------------------
  modules/_section.scss
--------------------------------------------------------

  Blog section styling - .section being the class that wraps around each article item

-----------------------------------------------------  */
.section {
  position: relative;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid #acacac; }
  .section:first-of-type {
    padding: 0;
    border-top: 0;
    border-bottom: 0; }
  .section:nth-child(odd) {
    background-color: #fff; }

/**

  @slant
--------------------------------------------------------
  modules/_slant.scss
--------------------------------------------------------

  Slant borders used to border white and gray sections.
  Dependent on relative parent

-----------------------------------------------------  */
.slant {
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  width: 100%;
  transform: translateY(-50%);
  z-index: 99; }

.slant__upper {
  position: absolute;
  bottom: 0.375rem;
  max-height: 2.1875rem;
  vertical-align: bottom;
  z-index: -1; }

.slant__lower {
  position: absolute;
  bottom: 0;
  max-height: 2.1875rem;
  vertical-align: bottom; }

/**

  @thumbnails
--------------------------------------------------------
  modules/_thumbnails.scss
--------------------------------------------------------

  Thumbnails are usually found within a grid or gallery,
  as a teaser or preview of more content. The common
  use is to have an image on top of a title and text
  below, while the entire thumbnail is an anchor link.

  Example: Thumbnail
  <a class="thumbnail" href="/some/article">
    <img class="thumbnail__media" src="http://fillmurray.com/400/200">
    <div class="thumbnail__caption">
      <h2>A caption title here</h2>
      <p>Some more text follows to tease the rest of
      the content</p>
    </div>
  </a>

-----------------------------------------------------  */
.thumbnail {
  display: block;
  padding: 0.1875rem;
  border: 0.0625rem solid rgba(0, 0, 0, 0.15);
  background: rgba(0, 0, 0, 0.1);
  text-decoration: none; }

/**

  @video
--------------------------------------------------------
  modules/_video-embeds.scss
--------------------------------------------------------

  Video Embeds

-----------------------------------------------------  */
.video-embed {
  display: block;
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 66.666%; }
  .video-embed iframe {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%; }

